export const getPOST = (getDATA) => { 
    return new HttpClient().post(`${GET_POST}`, {
        'dbTable': getDATA.dbTable,
        'wallId': localStorage.getItem('wallId'),
        'type': getDATA.type,
        'randomizePost': getDATA.randomizePost,
        'offset': getDATA.offset
    })
}



export const updateWall = (getdata) => {
    return new HttpClient().post(`${UPDATE_WALL_NAME}`, getdata)
}
