import React, { Component } from 'react'
import { UPDATE_EXTRA_DATA } from '../../../../actions/initActions';

export default class SelectPlatform extends Component {
    updateData = () => {
        try {
            const extra_data = this.props.userData?.user_data.user.extra_data ? JSON.parse(this.props.userData?.user_data.user.extra_data):{}; 
            if(extra_data && !extra_data.EMAIL_PLATFORM_SELECTED){
                UPDATE_EXTRA_DATA({EMAIL_PLATFORM_SELECTED:true}).then(res => {
                    this.props.getUserInfo_afterChanges()
                })
            }
        } catch (error) {
            console.log('error', error)
        }
    }
  render() {
    const {onSelectedPlatform, selectedPlatform} = this.props
    
    return (
        <div className="select_platform_sec mt-7">
            <h6 className="mb-4">Select Platforms</h6>
            <div className="campaign_platforms">
                <ul className="nav flex-column nav-pills" id="v-pills-tab" role="tablist" aria-orientation="vertical">
                    <li>
                        <button onClick={() => {onSelectedPlatform('klaviyo'); this.updateData()}} className="nav-link active"  id="v-pills-klaviyo-tab" data-bs-toggle="pill" data-bs-target="#v-pills-klaviyo" type="button" role="tab" aria-controls="v-pills-klaviyo" datatext="<p>Login to your <strong><a href='http://klaviyo.com' style='color:#0073e9;' target='_blank'>Klaviyo Account</a></strong>.</p><p>Next, choose the campaign name and recipients</p><p>Now, <strong>create</strong> a new template or choose a template from the template library.</p><p>Here select HTML and paste the copied UGC campaign code to the template.</p><p>Next, review all sections and click on <strong>Schedule</strong> or <strong>Send</strong>.</p>" aria-selected="true">
                            <img src="https://cloud.taggbox.com/taggshop/app/assets/media/email-campaign/logos/klaviyo.svg" alt="klaviyo" />
                        </button>
                    </li>
                    <li>
                        <button onClick={() => {onSelectedPlatform('omnisend'); this.updateData()}} className="nav-link" id="v-pills-omnisend-tab" data-bs-toggle="pill" data-bs-target="#v-pills-omnisend" type="button" role="tab" aria-controls="v-pills-omnisend" aria-selected="false" datatext="<p>Start by logging in to your <strong><a href='http://omnisend.com' style='color:#0073e9;' target='_blank'>Omnisend Account.</a></strong></p><p>Now proceed to the <strong>Campaign</strong> tab > click on the <strong>+ New Campaign</strong> button > choose to create the email campaign</p><p>On the settings stage of the email campaign, decide how you want it to look in your contacts' inboxes</p><p>Now select the email campaign template</p><p>From the content tab choose HTML and paste the generated UGC campaign code</p>
                                                        <p>Save the template. Choose the recipients and send the campaign</p>" >
                            <img src="https://cloud.taggbox.com/taggshop/app/assets/media/email-campaign/logos/omnisend.svg" alt="omnisend" />
                        </button>
                    </li>
                    <li>
                        <button onClick={() => {onSelectedPlatform('hubspot'); this.updateData()}} className="nav-link" id="v-pills-hubspot-tab" data-bs-toggle="pill" data-bs-target="#v-pills-hubspot" type="button" role="tab" aria-controls="v-pills-hubspot" aria-selected="false" datatext="<p>Login to your <strong><a href='https://www.hubspot.com/' style='color:#0073e9;' target='_blank'>HubSpot Account</a></strong></p><p>Now, navigate to <strong>Marketing</strong> > <strong>Email</strong></p><p>Next, click on <strong>Create</strong> email</p><p>From the drag and drop tab, choose a template based on the type of email you want to create</p><p>To add UGC to your template, click and drag a HTML element from the <strong>Content</strong> tab in the left panel directly into the email editor</p><p>Now, paste the generated UGC email campaign code</p><p>Lastly, apply the required changes and Voila! You will have successfully added the UGC campaign on email</p>" >
                            <img src="https://cloud.taggbox.com/taggshop/app/assets/media/email-campaign/logos/hubspot.svg" alt="hubspot" />
                        </button>
                    </li>
                    <li>
                        <button onClick={() => {onSelectedPlatform('active'); this.updateData()}} className="nav-link" id="v-pills-active-tab" data-bs-toggle="pill" data-bs-target="#v-pills-active" type="button" role="tab" aria-controls="v-pills-active" aria-selected="false" datatext="<p>Login to your <a href='http://activecampaign.com' style='color:#0073e9;' target='_blank'>Active Campaign</a> account. Here, click on ​<strong>Create a Campaign</strong></p><p>Next, choose a list and create a segment</p><p>Now, select a template for your campaign</p><p>Next, set <strong>Sender Details</strong></p><p>Customize your campaign. Drag and drop HTML block, and paste the generated UGC Campaign code</p><p>Lastly, review the <strong>Campaign Summary</strong> page and send your email</p>" >
                            <img src="https://cloud.taggbox.com/taggshop/app/assets/media/email-campaign/logos/active.svg" alt="activecampaign" />
                        </button>
                    </li>
                    <li>
                        <button onClick={() => {onSelectedPlatform('mailchamp'); this.updateData()}} className="nav-link" id="v-pills-mailchamp-tab" data-bs-toggle="pill" data-bs-target="#v-pills-mailchamp" type="button" role="tab" aria-controls="v-pills-mailchamp" aria-selected="false" datatext="<p>Login to your <a href='http://mailchimp.com' style='color:#0073e9;' target='_blank'>Mailchimp Account</a>. To create an email campaign, click on the <strong>Create</strong> icon</p><p>From the email dropdown click on <strong>Regular</strong> and give a name to your campaign</p><p>Next, add recipients and in the subject field enter a subject line for your email</p><p>Now design your email by choosing the template</p><p>Add HTML from the content block and paste the copied UGC campaign code here</p><p>Apply the required changes and Voila! You have successfully added the UGC campaign on email</p>" >
                            <img src="https://cloud.taggbox.com/taggshop/app/assets/media/email-campaign/logos/mailchamp.svg" alt="mailchamp" />
                        </button>
                    </li>
                    <li>
                        <button onClick={() => {onSelectedPlatform('getresesponse'); this.updateData()}} className="nav-link" id="v-pills-getresesponse-tab" data-bs-toggle="pill" data-bs-target="#v-pills-getresesponse" type="button" role="tab" aria-controls="v-pills-getresesponse" aria-selected="false" datatext="<p>Login to your <a href='https://www.getresponse.com/' style='color:#0073e9;' target='_blank'>GetResponse Account</a>. From the dashboard select <strong>Create Newsletter</strong></p><p>Provide an internal name for your message and select your linked list.</p><p>Start designing your message by clicking on <strong>Design</strong> message.</p><p>Choose if you want to use Predesigned templates, Your templates, Blank templates, or design the message using HTML editor.</p><p>For HTML, the editor will open where you can write your code, here paste the generated UGC email campaign code.</p><p>Save the message as draft or Click Send message/Schedule.</p>" >
                            <img src="https://cloud.taggbox.com/taggshop/app/assets/media/email-campaign/logos/getresesponse.svg" alt="getresesponse" />
                        </button>
                    </li>
                    <li>
                        <button onClick={() => {onSelectedPlatform(); this.updateData()}} className="nav-link" id="v-pills-other-tab" data-bs-toggle="pill" data-bs-target="#v-pills-other" type="button" role="tab" aria-controls="v-pills-other" aria-selected="false">
                            Other
                        </button>
                    </li>
                </ul>
            </div>
        </div>
    )
  }
}
