import { Modal } from "react-bootstrap";
import { useState } from "react";

import { toast } from "react-toastify"; 
import { shareCode } from "../../../../../../actions/publishAction"; 
import { DELETE_KEYS } from "../../../../../../actions/moderationActions";
import { TOAST_SUCCESS, TOAST_ERROR } from "../../../../../../utils";

 
const SendCodeByEmail = ({setOpenPopup, sharecode, from }) => {
 

  const [email, setEmail] = useState('');
  const [htmlCode, setHtmlCode] = useState(sharecode.emailCmp)
  const [loader, setLoader] = useState(false);

  const sendCode = () => {

  if (email.trim().length > 0) {

    const code = {
      'email': email,
      'type': from=='snapUp' ? 6 : 3,
      'htmlCode': htmlCode
    }

    shareCode(code).then((res) => {

      if (res.data.status) {
        setLoader(true);
        setTimeout(() => {
          DELETE_KEYS();
          if(from=='snapUp') TOAST_SUCCESS('Successfully shared.')
          else TOAST_SUCCESS('Email Campaign Code successfully share.')
          setLoader(false)
          setOpenPopup(false)
        }, 1000);
      }else{
        TOAST_ERROR(res.data.message);
      }
    })
  } else
    TOAST_ERROR('Please fill Email Id');
  }

  return (
    <>
      <Modal dialogClassName=" modal-dialog modal-dialog-centered modal-md personalizePopupModal dynamic-popup"
        size="lg" show={true}>
        <Modal.Body>
          <button
            type="button"
            className="close-modal btn-close close"
            data-bs-dismiss="modal"
            onClick={(e) => {setOpenPopup(false) }}
          >
          </button>
          <div className="p-8">
            <div className="pt-4 text-center">
            </div> <div className="mb-7 text-center">
              <h2 className="font-weight-boldest font-size-h2 mb-3">Send by email</h2>
              <p className="font-size-lg">Share this {from=='snapUp' ? 'URL' : 'embed code'} with your team. Enter the Email Id to send the {from=='snapUp' ? 'URL' : 'code'} directly to them</p>
            </div>
            <div className="from-group">
              <textarea name="data[Wall][emails]" placeholder="Ex: support@taggshop.com,info@taggshop.com" label="Emails" className="form-control form-control-lg" value={email} onChange={(e) => setEmail(e.target.value)} id="wallemails" style={{ height: '78px' }}></textarea> <span id="wallemailserror" className="errorMessage required">
              </span>
              <button type="submit" onClick={(e) => sendCode(e)} className={`${loader ? 'ladda-button spinner spinner-white spinner-right disabled ' : ''} btn btn-primary btn-lg w-100 mt-5`} data-factors-bind="true" data-factors-click-bind="true">Share Now</button>
            </div>
          </div>
        </Modal.Body>
      </Modal>
    </>
  )
}

export default SendCodeByEmail
