import axios from 'axios';
import HttpClient from "./http-client";
import { CREATE_EMAIL_API, UPDATE_EMAIL_API, DELETE_EMAIL_API, ALL_EMAIL_LIST_API, GET_POST } from './api'
import { CREATE_EMAIL_REDUCER, UPDATE_EMAIL_REDUCER, DELETE_EMAIL_REDUCER, ALL_EMAIL_LIST_REDUCER, ACTIVE_EMAIL_REDUCER, LOADING_EMAIL_REDUCER } from '../reducer/keys';
import { TOAST_DISMISS, TOAST_ERROR, TOAST_SUCCESS } from '../utils'; 
import store from '../store';
import { UPDATE_EXTRA_DATA } from './initActions';




export const CREATE_EMAIL = (name, callback, navigate) => {
    var obj = { "name": name }
    return (dispatch) => {
        new HttpClient().post(CREATE_EMAIL_API, obj).then((response) => {
            const { responseData, responseCode, responseMessage } = response.data
            if (responseCode == 200) {
                localStorage.setItem('emailCamp_id', responseData?.id)
                UPDATE_EXTRA_DATA({ emailCamp_id: responseData?.id })
                TOAST_DISMISS()
                TOAST_SUCCESS(responseMessage)
                dispatch({ type: CREATE_EMAIL_REDUCER, payload: responseData });
                dispatch({ type: ACTIVE_EMAIL_REDUCER, payload: responseData?.id });
                navigate(`/emailCampaign/publish/${localStorage.getItem('emailCamp_id')}`)
                callback(responseCode, responseMessage);
            }

        }).catch(error => {
            const { data } = error.response;
            TOAST_DISMISS();
            if (data.responseCode == 404) {
                TOAST_ERROR(data.responseMessage);
                callback(data.responseCode, data.responseMessage);
            }
            else {
                TOAST_ERROR("Something went wrong!")
            }
        });
    }
}




export const UPDATE_EMAIL = (id, name, callback) => {
    const obj = { "id": id, "name": name }

    return (dispatch) => {
        new HttpClient().post(UPDATE_EMAIL_API, obj).then((response) => {
            const { responseData, responseCode, responseMessage } = response.data
            if (responseCode == 200) {
                TOAST_DISMISS()
                TOAST_SUCCESS(responseMessage)
                dispatch({ type: UPDATE_EMAIL_REDUCER, payload: { id, responseData } });
            }
        }).catch(error => {
            console.log(error)
        });
    }
}


export const DELETE_EMAIL = (id, callback, navigate) => {
    const { emailList } = store.getState().emailList
    return (dispatch) => {
        new HttpClient().post(`${DELETE_EMAIL_API}/${id}`).then((response) => {
            const { responseData, responseCode, responseMessage } = response.data
            if (responseCode == 200) {
                TOAST_DISMISS()
                TOAST_SUCCESS(responseMessage)
                dispatch({ type: DELETE_EMAIL_REDUCER, payload: id });
                setTimeout(() => {
                    if (emailList && emailList?.length > 0) {
                        if (id == localStorage.getItem('emailCamp_id')) {
                            if (emailList[0]?.id == id) {
                                localStorage.setItem('emailCamp_id', emailList[1]?.id)
                                dispatch({ type: ACTIVE_EMAIL_REDUCER, payload: emailList[1]?.id });
                                UPDATE_EXTRA_DATA({ emailCamp_id: emailList[1]?.id })
                                navigate(`/emailCampaign/publish/${emailList[1]?.id}`)
                            } else {
                                localStorage.setItem('emailCamp_id', emailList[0]?.id)
                                dispatch({ type: ACTIVE_EMAIL_REDUCER, payload: emailList[0]?.id });
                                UPDATE_EXTRA_DATA({ emailCamp_id: emailList[0]?.id })
                                navigate(`/emailCampaign/publish/${emailList[0]?.id}`)
                            }
                        }

                    } else {
                        localStorage.setItem('emailCamp_id','')
                        UPDATE_EXTRA_DATA({ emailCamp_id: '' })
                        dispatch({ type: ACTIVE_EMAIL_REDUCER, payload: '' });
                        navigate(`/emailCampaign/publish/`)
                    }
                }, 1000);
                callback(responseCode, responseMessage);
            }

        }).catch(error => {
            console.log(error)
        });
    }
}







