export const GET_ALL_EMAIL_LIST = () => {
    var allwalls = store.getState()?.dashboardData?.dashboarddata?.allWalls
    return (dispatch) => {
        dispatch({ type: LOADING_EMAIL_REDUCER, payload: false })
        new HttpClient().get(ALL_EMAIL_LIST_API).then((response) => {
            const { responseData, responseCode } = response.data
            if (responseCode == 200) {
                var webData = responseData?.filter((x) => x.id == localStorage.getItem('emailCamp_id'));
                dispatch({ type: ALL_EMAIL_LIST_REDUCER, payload: responseData });
                dispatch({ type: LOADING_EMAIL_REDUCER, payload: true });
                dispatch({ type: ACTIVE_EMAIL_REDUCER, payload: localStorage.getItem('emailCamp_id') });
            }

        })
    }
}



export const CHANGE_EMAIL_WALL = (obj) => {
    return new HttpClient().post(GET_POST, obj)
}

export const UPDATE_EMAIL_DATA = (obj, callback) => {
    return (dispatch) => {
        new HttpClient().post(UPDATE_EMAIL_API, { ...obj, id: localStorage.getItem('emailCamp_id') }).then((response) => {
            const { responseData, responseCode, responseStatus } = response.data
            if (responseCode == 200) {
                TOAST_SUCCESS(responseStatus)
                dispatch({ type: UPDATE_EMAIL_REDUCER, payload: { id: localStorage.getItem('emailCamp_id'), responseData } });
                callback()
            }
        }).catch(error => {
            console.log(error)
        });
    }

}

